.doctor-section {
    padding: 32px;
    margin: 0 0 32px 0;
    text-align: center;
    background-color: white;
}
.dt-title-content {
    margin: 0 0 64px 0;
}
.dt-title {
    font-family: 'Poppins', sans-serif;
    font-size: 40px;
    font-weight: bold;
    letter-spacing: .7px;
}

.dt-titlenew {
    font-family: 'Poppins', sans-serif;
    margin:5%;
    font-size: 1.6em;
    font-weight: bold;
    letter-spacing: .7px;
}
.dt-titlenew > span {
    position: relative;
}
.dt-titlenew span::before {
    content: '';
    position: absolute;
    width: 64%;
    height: 4px;
    background-color: #9D2553;
    border-radius: 8px;
    left: 0;
    bottom: -12px;
}
.dt-title > span {
    position: relative;
}
.dt-title span::before {
    content: '';
    position: absolute;
    width: 64%;
    height: 4px;
    background-color: #9D2553;
    border-radius: 8px;
    left: 0;
    bottom: -12px;
}
.dt-description {
    margin: 64px auto 0;
    text-align: center;
    color: rgb(77, 75, 75);
    font-family: 'Rubik', sans-serif;
    font-size: 20px;
    letter-spacing: .8px;
    line-height: 1.8rem;
}
/* Doctor Cards */
.dt-cards-content {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 32px;
}
.dt-card {
    width: 332px;
    height: 400px;
    gap: 10px;
    text-align: left;
    font-family: 'Rubik', sans-serif;
}
.dt-card-img {
    width: 40%;
    height: 40%;
   
    background-position: center;
    background-color: #DDE5F8;
    border-radius: 20px;
    box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.3);
    border-radius: 50%;

}
.dt-card-name {
    margin: 12px 0 0 0;
    font-size: 22px;
    font-weight: bold;
    letter-spacing: .7px;
    text-decoration: underline;
  text-decoration-color: #310456;
}
.dt-card-title {
    margin: 6px 0 8px;
    color: #494949;
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
    letter-spacing: .7px;
}
.dt-card-stars {
    font-size: 18px;
    font-weight: bold;
    letter-spacing: .7px;
}
.dt-card-reviews {
    color: #5a5a5a;
    font-size: 18px;
    font-weight: bold;
    letter-spacing: .7px;
}
.mik-img{
    width: 32px;
    height: 40px;
    gap: 30px;
}
/* Responsive */
@media screen and (max-width: 700px) {
    .dt-title {
        font-size: 32px;
    }
    .dt-description {
       
        font-size: 18px;
    }
    .dt-card-name {
        margin: 12px 0 0 0;
        font-size: 18px;
        font-weight: bold;
        letter-spacing: .7px;
        text-decoration: underline;
      text-decoration-color: #310456;
    }
    .dt-card-title {
        margin: 6px 0 8px;
        color: #494949;
        font-family: 'Poppins', sans-serif;
        font-size: 15px;
        letter-spacing: .7px;
    }
   
  
    .dt-card-reviews {
        color: #5a5a5a;
        font-size: 12px;
        font-weight: bold;
        letter-spacing: .7px;
    }
}
